<template>
    <el-dialog
        :title="$t('msg.excel.roleDialogTitle')"
        :model-value="modelValue"
        @close="closed"
    >
        <el-checkbox-group v-model="userRoleTitleList">
            <el-checkbox
                v-for="item in allRoles"
                :key="item.id"
                :label="item.title"
            ></el-checkbox>
        </el-checkbox-group>
        <template #footer>
            <div class="dialog-footer">
                <el-button @click="closed">
                    {{ $t('msg.universal.cancel') }}
                </el-button>
                <el-button type="primary" @click="onConfirm">
                    {{ $t('msg.universal.confirm') }}
                </el-button>
            </div>
        </template>
    </el-dialog>
</template>

<script setup>
import { getRoleList } from '@/api/role';
import { userRoles, updateRole } from '@/api/userManger';

import { watchSwitchLang } from '@/utils/i18n';
import { ElMessage } from 'element-plus';
import { ref, watch } from 'vue';
import { useI18n } from 'vue-i18n';

const props = defineProps({
    modelValue: {
        type: Boolean,
        required: true
    },
    userId: {
        type: String,
        required: true
    }
});

// 
const allRoles = ref([]);
const getRoles = async () => {
    try {
        allRoles.value = await getRoleList();
    } catch (error) {
        allRoles.value = [
            {
                id: '1',
                title: '',
                describe: ''
            },
            {
                id: '2',
                title: '',
                describe: ''
            },
            {
                id: '3',
                title: '',
                describe: ''
            },
            {
                id: '4',
                title: '',
                describe: ''
            },
            {
                id: '5',
                title: '',
                describe: ''
            },
            {
                id: '6',
                title: '',
                describe: ''
            }
        ];
    }
};
getRoles();
watchSwitchLang(getRoles);

// 
const userRoleTitleList = ref([]);
const getUserRoles = async () => {
    try {
        const res = await userRoles(props.userId);
        userRoleTitleList.value = res.role.map((item) => item.title);
    } catch (error) {
        const res = {
            role: [
                {
                    id: '2',
                    title: ''
                }
            ],
            _id: '6124eacc0dafe231304546be'
        };
        userRoleTitleList.value = res.role.map((item) => item.title);
        console.log('[  userRoleTitleList.value]', userRoleTitleList.value);
    }
};

const emits = defineEmits(['update:modelValue', 'updateRole']);
const closed = () => {
    emits('update:modelValue', false);
};
const i18n = useI18n();
const onConfirm = async () => {
    const roles = userRoleTitleList.value.map((title) => {
        return allRoles.value.find((role) => role.title === title);
    });
    try {
        await updateRole(props.userId, roles);
        ElMessage.success(i18n.t('msg.role.updateRoleSuccess'));
        //  
        emits('updateRole');
    } catch (error) {
        ElMessage.success(i18n.t('msg.role.updateRoleSuccess'));
    }
    closed();
};
watch(
    () => props.userId,
    (value) => {
        if (value) {
            getUserRoles();
        }
    }
);
</script>

<style lang="scss" scoped></style>
