    display: flex;
    justify-content: center;
    margin-top: 100px;
    .excel-upload-input {
        display: none;
        z-index: -9999;
    }
    .btn-upload,
    .drop {
        border: 1px dashed #bbb;
        width: 350px;
        height: 160px;
        text-align: center;
        line-height: 160px;
    }
    .drop {
        line-height: 60px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        color: #bbb;
        i {
            font-size: 60px;
            display: block;
        }
    }
}
</style>
